class Utils {
  //
  constructor(){
    this.instance=null;
  }
  //
  static getInstance(){
    if(!this.instance){
      this.instance = new Utils();
    }
    return this.instance;
  }

  /**
   * 
   * @returns {number}
   */
  getNowTimeStamp(){
    return new Date().getTime()
  }

  /**
   * URL
   * @returns {string}
   */
  getUrlParam(){
    return document.location.toString().split("?")[1]
  }


  /**
   * localStorage
   */
  setStore(name, content){
    if (!name) return
    if (typeof content !== 'string') {
      content = JSON.stringify(content)
    }
    window.localStorage.setItem(name, content)
  }

  /**
   * localStorage
   */
  getStore(name){
    if (!name) return
    return window.localStorage.getItem(name)
  }
  /**
   * localStorage
   */
  removeStore(name){
    if (!name) return
    window.localStorage.removeItem(name)
  }
  /**
   * sessionStorage
   */
  setSessionStore(name, content){
    if (!name) return
    if (typeof content !== 'string') {
      content = JSON.stringify(content)
    }
    window.sessionStorage.setItem(name, content)
  }

  /**
   * sessionStorage
   */
  getSessionStore(name){
    if (!name) return
    return window.sessionStorage.getItem(name)
  }
  /**
   * sessionStorage
   */
  removeSessionStore(name){
    if (!name) return
    window.sessionStorage.removeItem(name)
  }

  /**
   * 
   * 
   * @param property  
   * @param sort          boolean
   * @returns {function(*, *): number}
   */
  compareSort(property,sort){
    return (a,b) =>{
      return sort?a[property] - b[property]:b[property] - a[property]
    }
  }

  /**
   * 
   */
  isNull(str){
    return str !== undefined && str !== null && str != ''
  }

  /**
   * guid
   * @returns {string}
   */
  guid(){
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      var r = Math.random() * 16 | 0,
        v = c == 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    })
  }
  /**
   *  Android/IOS
   */
  getUserDevice = () => {
    let request = ''
    let u = navigator.userAgent
    let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 //androiduc
    let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) //ios
    if (isAndroid) {
      request = {
        isAndroid:true
      }
    }
    if (isiOS) {
      request = request = {
        isIOS:true
      }
    }
    return request
  }
}
const UTILS = Utils.getInstance()

export default UTILS
